.birthdayContainer {
  display: flex;
  align-items: center;
  cursor: pointer;

  .birthdayImg {
    width: 40px;
    height: 40px;
    margin-right: 10px;
  }
  .birthdayText {
    font-weight: 300;
    font-size: 15px;
  }
}

.rightbarAd {
  width: 100%;
  border-radius: 10px;
  margin: 30px 0;
  cursor: pointer;
  &:active {
    border: 3px solid red;
  }
}

.rightbarFriendList {
  padding: 0;
  margin: 0;
  list-style: none;
}

.rightbarTitle {
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
  margin-bottom: 20px;
  color: blue;
}

.rightbarInfo {
  margin-bottom: 30px;
  .rightbarInfoItem {
    margin-bottom: 10px;

    .rightbarInfoKey {
      font-weight: 500;
      margin-right: 15px;
      color: #0c0101;
    }

    .rightbarInfoValue {
      font-weight: 700;
      color: #0c0101;
    }
  }
}

.rightbarFollowings {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .rightbarFollowing {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
    margin: 0.5rem;

    .rightbarFollowingImg {
      width: 100px;
      height: 100px;
      object-fit: cover;
      border-radius: 5px;
    }
  }
}
