.profile {
  display: flex;

  .profileRight {
    flex: 9;

    .profileCover {
      height: 420px;
      position: relative;

      .profileCoverImg {
        width: 100%;
        height: 450px;
        object-fit: cover;
      }

      .profileUserImg {
        width: 200px;
        height: 200px;
        border-radius: 50%;
        object-fit: cover;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        top: 150px;
        border: 3px solid white;
      }
    }
    .profileInfo {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .profileInfoName {
        font-size: 35px;
      }

      .profileInfoDesc {
        font-weight: 600;
      }
    }

    .profileRightBottom {
      display: flex;
    }
  }
}

@media only screen and (max-width: 700px) {
  .leftSide {
    display: none;
  }
}
