.mainPost {
    width: 40rem;
    border: transparent;
    background-color: whitesmoke;

    .userInfo {
        display: flex;
        margin: 0 1rem;
        justify-content: space-between;
        align-items: center;

        .userInfoContainer {
            display: flex;
            align-items: center;

            a {
                text-decoration: none;
                color: black;

                &:hover {
                    text-decoration: underline;
                }
            }

            .profilePictureImg {
                height: 3rem;
                width: 3rem;
                border-radius: 50%;
                object-fit: cover;
                margin-right: 0.3rem;
            }
        }

        .contextMenu {
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;

            &:hover {
                border: 1px solid teal;
            }
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        p {
            margin: 0.2rem 1rem;
            align-self: flex-start;
            text-align: justify;
        }

        .imageWrapper {
            position: relative;

            .mainPostImage {
                max-width: 100%;
            }

            .infoIcon {
                position: absolute;
                right: 3rem;
                color: teal;
                bottom: -1rem;
            }
        }
    }

    .reacts {
        margin: 0.5rem 0;
        display: flex;
        justify-content: space-around;
    }

    .reactsActions {
        display: flex;
        justify-content: space-around;
        margin: 0.5rem 0;

        .reaction {
            display: flex;
            cursor: pointer;
            padding: 0.2rem;
            border-radius: 5px;

            &:hover {
                background-color: lightgray;
            }

            &.blue {
                color: blue;
            }
        }
    }
}